<template>
    <div>
        <el-card class="box-card" v-loading="loading">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/form' }">总览</el-breadcrumb-item>
            </el-breadcrumb>
            <el-divider></el-divider>

            <!-- <el-button size="mini" @click="tables(tableData.length)">选中几条</el-button> -->

            <el-table
                :data="tableData.slice((currentPage - 1) * pagesize, currentPage*pagesize)"
                border
                style="width: 100%"
                @selection-change="handleSelectionChange"
                >

                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-row>
                                <el-col :md="12">
                                    <el-form-item label="创建时间">
                                        <template>
                                            <i  class="el-icon-time" /> <span>{{props.row.createdAt | date}}</span>
                                        </template>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="12">
                                    <el-form-item label="修改时间">
                                        <template>
                                            <i  class="el-icon-time" /> <span>{{props.row.updatedAt | date}}</span>
                                        </template>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="12">
                                    <el-form-item label="浏览次数">
                                        <i class="el-icon-view"></i> <span class="color-green">{{ props.row.browse }}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="24">
                                    <el-form-item label="详情轮播图">
                                        <template>
                                            <el-row>
                                                <el-col :md="6" style="margin:0 10px;"
                                                 v-for="item in props.row.skills" :key="item._id">
                                                 <el-image :src="item.image">
                                                </el-image>
                                                </el-col>
                                            </el-row>
                                        </template>
                                    </el-form-item>
                                </el-col>
                            </el-row>

                        </el-form>
                    </template>
                    </el-table-column>


              
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column  prop="_id" label="编号" width="230">
                </el-table-column>
                
                <el-table-column label="商品分类" width="100">
                    <template slot-scope="scope">
                        <el-tag>{{scope.row.categoryes}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="商品名称" width="150" >
                </el-table-column>
                <el-table-column label="图片" width="120">
                    <template slot-scope="scope">
                        <img :src="scope.row.image" alt="" style="width:98px;">
                    </template>
                </el-table-column>
                <el-table-column label="价格" width="100">
                    <template slot-scope="scope">
                        ￥：{{scope.row.price}}
                    </template>
                </el-table-column>
                 <el-table-column  prop="stock" label="库存" width="80">
                </el-table-column>
                <el-table-column prop="describe" label="描述"></el-table-column>
                 <el-table-column prop="user" label="推荐指数" width="180">
                     <template slot-scope="scope">
                         <el-rate
                            v-model="scope.row.recommend"
                            disabled
                            show-score
                            text-color="#ff9900"
                            score-template="{value}">
                        </el-rate>
                     </template>
                </el-table-column>
                <el-table-column label="state" width="100" >
                    <template slot-scope="scope" class="state-item">
                        <el-tag type="success" v-if="scope.row.state == true" >上架中</el-tag>
                        <el-tag type="warning" v-else-if="scope.row.state == false">待上架</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="上架时间" width="200">
                    <template slot-scope="scope">
                        <i  class="el-icon-time" /> <span>{{scope.row.updatedAt | date}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="operation"
                 v-if="flag > 10 ? true : false">
                <template slot-scope="scope">
                    <el-button type="danger" @click.native.prevent="offline(scope.row)" class="el-icon-delete-solid"
                    >下架</el-button>
                   
                </template>
                </el-table-column>
            </el-table>

            <div class="block">
                <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[1, 10, 20,50,100]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length">
                </el-pagination>
            </div>
        </el-card>
    </div>
</template>
<script>
import dayjs from 'dayjs'      
export default {
    filters:{
         // 转换日期格式
        date(val){
        return dayjs(val).format("YYYY-MM-DD hh:mm")
        }
    },
    data() {
        return {
        flag:'',
        loading:true,
        currentPage:1,
        pagesize:10,  
        tableData: [],
        selections:[]
        }
    },
    created(){
        setTimeout(()=>{
            this.loading = false
        },500)
        this.flags()
        this.list()
    },
    methods: {
        //  过滤没上架的商品
        async list(){
            const res = await this.$http.get('/goodsList')
            // const arr = res.data.filter(e => e.state == true)
            this.tableData = res.data
        },
        async offline(e){
            e.state = false
            const res = await this.$http.put(`/goods/${e._id}`,e)
            this.$message({
                type:'warning',
                message:'已下架'
            })
            this.list()
        },
        //  权限
        flags(){
            const power = JSON.parse(window.localStorage.getItem('power'))
            this.flag = power
            console.log(this.flag)
        },
        handleClick(row) {},
        handleSizeChange(val) {
             this.pagesize = val;
        },
        handleCurrentChange(val) {
            this.currentPage = val;
        },
        handleSelectionChange(val){
            this.selections = val.map(e =>{
                return e
            })
        },
        tables(){
            let item = this.selections
            this.$message({
                message:`你选中了${item.length}条数据`,
                type:'success'
            })
        }
    },
    
}
</script>
<style lang="less" scoped>
/deep/td{
    color: #7b7b7b;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>